.confirm-box {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.6);
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
  -webkit-transform: translate3d(0, 100%, 0);
          transform: translate3d(0, 100%, 0);
}
.confirm-box .box {
    margin-top: 15%;
    background-color: rgba(255, 255, 255, 0.88);
    border-radius: 6px;
    padding: 25px 35px;
}
.confirm-box.active {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
.big-btn {
  border: 3px solid #fff;
  border-radius: 100%;
  cursor: pointer;
  display: inline-block;
  font-weight: bolder;
  transition: background 0.18s;
  text-shadow: 0 1px 0 rgba(90, 74, 74, 0.7);
}
.big-btn-start {
  background: #0EC518;
  box-shadow: 0 -2px 0 3px #0b9512 inset, 0 5px 5px rgba(0, 7, 1, 0.17), 0 15px rgba(255, 255, 255, 0.25) inset;
}
.big-btn-stop {
  background: #f1b434;
  box-shadow: 0 -2px 0 3px #c7910f inset, 0 5px 5px rgba(103, 73, 0, 0.17), 0 15px rgba(255, 255, 255, 0.25) inset;
}
.big-btn-blue {
  background: #108FE8;
  box-shadow: 0 -2px 0 3px #0d72b8 inset, 0 5px 5px rgba(3, 25, 41, 0.17), 0 15px rgba(255, 255, 255, 0.25) inset;
}
.big-btn-blur {
  background: #8f9294;
  box-shadow: 0 -2px 0 3px #727373 inset, 0 5px 5px rgba(3, 25, 41, 0.17), 0 15px rgba(255, 255, 255, 0.25) inset;
}
.score {
  color: #6d6969;
  position: absolute;
  right: 20px;
  top: 20px;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
  background-color: #ffb401;
}
.score .pp {
    letter-spacing: 2px;
    font-size: 28px;
    color: #65483d;
}
.score-show {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.qrcode-img {
  position: absolute;
  bottom: 20px;
  right: 20px;
}
