    -webkit-box-flex: 1;
            flex: 1;
    display: -webkit-box;
    display: flex;
    padding: 0;
    overflow: hidden;
}
.wifi-box .el-dialog__header {
    display: none;
}
.wifi-box .el-dialog--small {
    width: 78%;
}
.wifi-box .wifi-item {
    padding: 10px 10px 10px 15px;
    border-bottom: 1px solid #ebebed;
    font-weight: bold;
    display: -webkit-box;
    display: flex;
    -webkit-box-pack: justify;
            justify-content: space-between;
    cursor: pointer;
